.faq {
  border-top: 1px solid rgba(54, 56, 65, 0.1);
  border-bottom: 1px solid rgba(54, 56, 65, 0.1);
}
.faq__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.faq__item {
  position: relative;
  border-bottom: 1px solid rgba(54, 56, 65, 0.1);
  padding-right: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
  cursor: pointer;
}
.faq__item:hover .faq__bold {
  color: #18181C;
}
.faq__item:last-child {
  border-bottom: 0;
}
.faq__item--active .faq__dscr {
  display: block;
}
.faq__item--active .faq__svg {
  transform: rotate(90deg);
}
.faq__bold {
  font-family: "Roboto", "Georgia", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: 0.06em;
  text-align: left;
  color: #757C84;
  text-transform: uppercase;
  transition: all ease-in-out 0.3s;
}
.faq__svg {
  position: absolute;
  top: 16px;
  right: 0;
  transition: all ease-in-out 0.3s;
}
.faq__dscr {
  display: none;
  font-family: "Roboto", "Georgia", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 25px;
  letter-spacing: 0.01em;
  text-align: left;
  color: grey;
  padding-top: 10px;
  margin: 0;
}
.social {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style: none;
}
.social .social__icon {
  fill: #ffffff;
  transition: all ease-in-out 0.3s;
}
.social .social__icon:hover {
  fill: #EC3E5A;
}
.social__item {
  margin-right: 30px;
}
.social__item:last-child {
  margin-right: 0;
}
.tabs-list {
  display: flex;
  padding: 0;
  list-style: none;
}
.tabs-list__btn {
  padding: 0;
  background-color: transparent;
  border: none;
  font-family: "Roboto", "Georgia", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 0.01em;
  padding: 15px 40px;
  color: #282830;
  cursor: pointer;
  transition: all ease-in-out 0.3s;
}
.tabs-list__btn:hover {
  background-color: #f16c82;
  color: #ffffff;
}
.tabs-list__btn--active {
  background-color: #EC3E5A;
  color: #ffffff;
}
.players {
  flex-grow: 1;
  max-width: 950px;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style: none;
}
.players__item {
  order: 3;
}
.players__item:nth-child(even) {
  background-color: #ffffff;
}
.players__item--start {
  padding: 10px 25px;
  order: 1;
}
.players__item--order-top {
  order: 2;
  border: 1px solid #20A95A;
}
.players__item--order-top .players__link {
  padding-top: 14px;
  padding-bottom: 14px;
}
.players__item:hover .players__data {
  color: #EC3E5A;
}
.players__link {
  display: flex;
  align-items: center;
  padding: 15px 25px;
  text-decoration: none;
}
.players__dim {
  font-family: "Roboto", "Georgia", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0.02em;
  text-align: left;
  margin-right: 47px;
  color: #757C84;
}
.players__dim:first-child {
  text-align: center;
  min-width: 32px;
  margin-right: 34px;
}
.players__dim:last-child {
  margin-right: 0;
}
.players__index {
  font-family: "Roboto", "Georgia", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 0.01em;
  text-align: center;
  color: #282830;
  min-width: 32px;
  margin-right: 34px;
}
.players__data {
  display: flex;
  align-items: center;
  font-family: "Roboto", "Georgia", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 0.01em;
  text-align: left;
  color: #282830;
}
.players__data:hover {
  color: #EC3E5A;
}
.players__svg {
  margin-right: 10px;
}
.players__name {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
